@import url(https://fonts.googleapis.com/css2?family=Texturina:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display+SC:ital,wght@0,400;0,700;0,900;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caudex);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  color: white;
  text-align: center;
  margin: 0px;
  padding: 0px;
}

*::-webkit-scrollbar {
  display: none;
  box-sizing: border-box;
}

.page {
  overflow-x: hidden;
  height: 100vh;
  width: 100vw;
  scroll-snap-align: start;
  -ms-scroll-snap-type: block mandatory;
      scroll-snap-type: block mandatory;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  position: relative;
}

.fullscreen {
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.opacity-10 {
  opacity: 0.1;
}

.z-10 {
  z-index: 10;
}

.z-20 {
  z-index: 20;
  position: absolute;
  animation: float 4s ease-in-out infinite;
}

.z-30 {
  z-index: 30;
  position: absolute;
  animation: float 4s ease-in-out infinite reverse;
}

@keyframes float {
  0% {
    transform: translate(0%, 0%);
  }
  50% {
    transform: translate(0%, -4%);
  }
  100% {
    transform: translate(0%, 0%);
  }
}

